@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

*{
  margin: 0;
}
body {
  font-family: "Poppins", sans-serif;
}

.container{
  max-width: 1280px;
  margin: 0 auto;
}

button{
  all: unset;
  padding: 4px 25px;  
  background-color: #D01C28;
  height: 32px;
  color: white;
  font-weight: 500;
}

nav {
  display: flex;
  justify-content: space-between;
  height: 72px;
  align-items: center;
}

ul {
  display: flex;
  gap: 24px;
  list-style: none;
}

.hero {
  display: flex;
  align-items: center;
  height: calc(100vh - 72px);
}

h1 {
  font-weight: 800;
  font-size: 108px;
  line-height: 102px;
}

p {
  font-weight: 600;
  color: gray;
  max-width: 400px;
}

.hero-btn {
  display: flex;
  gap: 40px;
}

.secondary-btn {
  background: transparent;
  color: gray;
  border: 1px solid gray;
}

.hero-content {
  display: flex;
  flex-direction: column;
  gap: 36px;
}

.brand-icons {
  display: flex;
  gap: 16px;
}

